        var length = this.$route.matched.length;
        // 正则匹配以/开头和结尾的任意路由 例 /home/
        var reg = /^\/.*\/$/g;
        // 正则匹配最后一级路由, 如果最后一级路由是/01/这样子就返回ture
        var showIcon = reg.test(this.$route.matched[length - 1].path);
